* {
    margin: 0;
    padding: 0;
    font-family : arial;
    cursor : default;
}
.allCenter {
    display : flex;
    justify-content : space-around;
    align-items : center;
}
.flex {
    display : flex;
}
.space-around {
    justify-content : space-around;
}
.flex-wrap {
    flex-wrap :wrap-reverse;
}
.pointer{
    cursor: pointer;
}
a,Link{
    color:black;
    text-decoration: none;
}