/* For Nav.jsx */
.nav{
    background:linear-gradient(-90deg,white, #a7a7ff,white, #a7a7ff);
    font-size: 200%;
    display:flex;
    flex-wrap:wrap;
}
.navLinks{
    outline:2px solid blue;
    border-radius: 5px;
    margin:5px;
    color:black;
    
}

/* For Home.jsx */
.infoBox {
    height : 100vh;
    width : 100vw;
    overflow : auto;
    background: linear-gradient(-90deg,rgb(162, 162, 255), white);
    margin:0 5px;
}
.infoBox img {
    width : 22vw;
    min-width : 150px;
}
.infoBox h1 {
    margin-bottom : 3px;
    font-size : 60px;
}
.infoBox h2 {
    margin-bottom : 3px;
    font-size : 40px;
}
.infoButton {
    background-color: rgb(157, 85, 234);
    border : 0;
    border-radius: 30px;
    padding: 6px;
    margin-top: 70px;
    margin-right : 18px;
    color : white;
    font-size : 35px;
    transition : all 0.5s ease;
}
.infoButton:hover {
    cursor : pointer;
    background-color : red;
    padding : 6px;
    animation : hoverEffects 0.5s infinite ease alternate;
}
@keyframes hoverEffects{
    0% {padding : 4px;}
    100% {padding : 6px;}
}
.infoBox .info h2 {
    /* text-shadow : 1px 1px white, -1px 1px white, 1px -1px white, -1px -1px white; */
    -webkit-text-stroke: 1px black;
    color:transparent;
}
@media only screen and (max-width: 710px) {
    .myImage , .myImage img {
        width : 50vw;
    }
  }

/* For Contact.jsx */
.contactbox{
    height:100vh;
    width:100vw;
    flex-wrap:wrap;
    margin: 0 4px;
    background: linear-gradient(-90deg,rgb(162, 162, 255), white);
}
.contactbox div{
    width:25vw;
}
.contactbox img{
    width:90%;
}
.contactbox p{
    text-align: center;
    margin:100px 0;
    font-size: 5vw;
}
@media only screen and (max-width: 310px) {
    .contactbox div{
        width:50vw;
        margin:0;
    }
    .contactbox img{
        width:90%;
    }
    .contactbox p{
        text-align: center;
        margin:10px 0;
        font-size: 5vw;
    }
  }

/* For Projects.jsx*/
#projects{
    background: linear-gradient(-90deg,rgb(162, 162, 255), white);
    height:max(100vh);
    width:100vw;
    display:flex;
    flex-wrap:wrap;
    justify-content: space-around;
    /* align-items: center; */
}
#projects div{
    width:200px;
    height:fit-content;
    padding:30px;
    outline:2px solid blue;
    border-radius: 6px;
    margin:15px;
}